import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        env: 'production',
        eventId: null,
        tickets: [],
        event: { name: '', personalize: false },
        database: {}
    },

    mutations: {
        setEventId(state, id) {
            state.eventId = id
        },

        setEnv(state, env) {
            state.env = env
        },

        setTickets(state, tickets) {
            state.tickets = tickets
        },

        incrementTicketAmount(state, index) {
            let tickets = [...state.tickets]
            let max = 10

            if (tickets[index].protection_quantity) {
                max = Math.min(max, tickets[index].protection_quantity)
            }
            if (tickets[index].amount < max) {
                tickets[index].amount++
            }
            state.tickets = tickets
        },

        decreaseTicketAmount(state, index) {
            let tickets = [...state.tickets]
            if (tickets[index].amount > 0) {
                tickets[index].amount--
            }
            state.tickets = tickets
        },

        refreshTickets(state) {
            const tickets = [...state.tickets]
            state.tickets = tickets
        },

        setEvent(state, event) {
            state.event = event
        },

        setDatabase(state, database) {
            state.database = database
        }
    },

    getters: {
        URL: state => {
            switch (state.env) {
                case 'local':
                    return 'http://localhost:8000'

                case 'staging':
                    return 'https://venoot-stage.work'

                case 'production':
                    return 'https://venoot-pro.work'
            }
        },

        totalPrice: state => {
            if (!state.tickets) {
                return 0
            }

            let totalPrice = 0
            let currency = null
            state.tickets.forEach(ticket => {
                if (ticket.amount > 0) {
                    if (!currency) {
                        currency = ticket.currency
                    } else if (currency != ticket.currency) {
                        currency = 'conflicted'
                        return
                    }
                }

                totalPrice += ticket.price * ticket.amount

                if (ticket.discount && ticket.discount.id !== 0) {
                    switch (ticket.discount.type) {
                        case 'flat':
                            totalPrice -= ticket.discount.type_value
                            break

                        case 'percentual':
                            totalPrice -=
                                (ticket.amount *
                                    ticket.price *
                                    ticket.discount.type_quantity) /
                                100

                            break
                    }
                }
            })

            if (totalPrice === 'NaN') totalPrice = 0
            return currency === 'conflicted' ? 'Moneda no es unica' : totalPrice
        }
    },
    actions: {}
})
