<script>
import { mapState, mapMutations, mapGetters } from 'vuex'
import { ValidationProvider, extend, localize } from 'vee-validate'
import es from 'vee-validate/dist/locale/es.json'
import { required, email, min } from 'vee-validate/dist/rules'
import axios from 'axios'
import { google, ics } from 'calendar-link'
import { BIconGoogle, BIconCalendar } from 'bootstrap-vue'

export default {
    name: 'App',
    props: ['id', 'env'],

    components: {
        BIconGoogle,
        BIconCalendar,
        ValidationProvider
    },

    data: () => ({
        value: null,
        fileObj: null,
        uploading: false,
        loading: false,
        loadingCupon: false,
        loadingKeyValue: false,
        valid: false,
        validCupon: false,
        validKeyValue: false,
        currentCupon: null,
        missingQuantity: 0,
        dialogOverlay: false,
        currentTicket: { discount: {} },
        currentKey: null,
        keyAccepted: [],
        emailAccepted: false,
        otherErrors: [],
        showDiscounts: false,

        user: {},
        defaultParticipant: {},
        participants: [],
        participantIndex: 0,

        names: [],
        uuids: [],
        boughtTickets: [],
        remote: []
    }),

    computed: {
        ...mapState(['tickets', 'eventId', 'event', 'database']),
        ...mapGetters(['totalPrice', 'URL']),

        triggerValue() {
            if (!this.currentTicket || !this.currentTicket.discount) {
                return false
            }

            return this.currentTicket.discount.trigger_value
        },

        fields() {
            if (this.showDiscounts) {
                return [
                    { label: 'Nombre', key: 'name' },
                    { label: 'Quedan', key: 'left', width: '100px' },
                    { label: 'Valor', key: 'price', width: '100px' },
                    { label: 'Descuento', key: 'discount', width: '200px' },
                    { label: 'Subtotal', key: 'subtotal', width: '100px' },
                    {
                        label: 'Cantidad',
                        key: 'amount',
                        aligth: 'center',
                        width: '220px'
                    }
                ]
            } else {
                return [
                    { label: 'Nombre', key: 'name' },
                    { label: 'Quedan', key: 'left', width: '100px' },
                    { label: 'Valor', key: 'price', width: '100px' },
                    { label: 'Subtotal', key: 'subtotal', width: '100px' },
                    {
                        label: 'Cantidad',
                        key: 'amount',
                        aligth: 'center',
                        width: '220px'
                    }
                ]
            }
        },

        currentCurrency() {
            let currency = null
            this.tickets.forEach(ticket => {
                if (ticket.amount > 0) {
                    if (!currency) {
                        currency = ticket.currency
                    } else if (currency != ticket.currency) {
                        currency = 'conflicted'
                        return
                    }
                }
            })

            if (currency === 'conflicted') {
                return ''
            } else {
                if (!currency) return ''
                else if (currency === 'CPL') return '$'
                else if (currency === 'USD') return 'USD '
            }

            return ''
        },

        primaryPresent() {
            for (var participant of this.participants) {
                if (participant.primary) return true
            }

            return false
        },

        eventLink() {
            return {
                title: this.event.name,
                start: Date.parse(
                    this.event.start_date + ' ' + this.event.start_time
                )
            }
        }
    },

    methods: {
        ...mapMutations([
            'setEventId',
            'setEnv',
            'setTickets',
            'setEvent',
            'refreshTickets',
            'incrementTicketAmount',
            'decreaseTicketAmount',
            'setDatabase'
        ]),

        googleLink(uuid, remote) {
            return google({
                ...this.eventLink,
                description: remote
                    ? `<a href="${this.URL}/venoot-chat?uuid=${uuid}">Al Evento</a>`
                    : `<a href="${this.URL}/qr/${uuid}">Al QR</a>`
            })
        },

        icsLink(uuid, remote) {
            return ics({
                ...this.eventLink,
                description: remote
                    ? `<a href="${this.URL}/venoot-chat?uuid=${uuid}">Al Evento</a>`
                    : `<a href="${this.URL}/qr/${uuid}">Al QR</a>`
            })
        },

        clearOtherPrimaries(val) {
            if (!val) return
            for (var i = 0; i < this.participants.length; i++) {
                if (i === this.participantIndex) continue

                this.participants[i].primary = false
            }
        },

        showSubmitDialog() {
            let currency = null
            let ticketTitles = []
            let ticketTitlesIndex = 0
            this.tickets.forEach(ticket => {
                if (ticket.amount > 0) {
                    if (!currency) {
                        currency = ticket.currency
                    } else if (currency != ticket.currency) {
                        currency = 'conflicted'
                        this.$refs.dialogOneCurrency.show()
                        return
                    }

                    for (var j = 0; j < ticket.amount; j++) {
                        ticketTitles[ticketTitlesIndex] =
                            ticket.name + ' #' + (j + 1)
                        ticketTitlesIndex++
                    }
                }
            })

            if (currency === 'conflicted') return

            const participantAmount = this.event.personalize
                ? this.tickets.reduce(function(prev, cur) {
                      return prev + cur.amount
                  }, 0)
                : 1
            this.participants = Array(participantAmount)
            for (var i = 0; i < participantAmount; i++) {
                this.participants[i] = Object.assign(
                    {},
                    this.defaultParticipant
                )
                this.participants[i].ticketName = ticketTitles[i]

                if (i === 0) {
                    this.participants[i].primary = true
                } else {
                    this.participants[i].primary = false
                }
            }

            this.$refs.dialog.show()
        },

        cancelSubmitDialog() {
            this.$refs.dialog.hide()
            this.refreshTickets()
            this.participants = Array()
        },

        getValidationState({ dirty, validated, valid = null }) {
            return dirty || validated ? valid : null
        },

        uploadPDF(evt, key) {
            setTimeout(() => {
                if (this.fileObj) {
                    var formData = new FormData()
                    formData.append('pdf', this.fileObj, this.fileObj.name)

                    var req = new XMLHttpRequest()
                    req.addEventListener('load', evt => {
                        this.uploading = false
                        this.user[key] = JSON.parse(
                            evt.target.responseText
                        ).name
                    })

                    req.open('POST', `${this.URL}/api/uploadFormPDF`)
                    req.send(formData)
                    this.uploading = true
                } else {
                    this.user[key] = null
                }
            }, 250)
        },

        checkKey(ticket) {
            this.$refs.dialogProtectionKey.hide()
            this.$refs.spinning.show()
            axios
                .post(`${this.URL}/api/tickets/${ticket.id}/check-key`, {
                    key: this.currentKey
                })
                .then(response => {
                    if (response.data.participant) {
                        this.user = response.data.participant.data
                        this.$refs.dialogKeyAccepted.show()
                        this.keyAccepted.push(ticket.id)
                    } else {
                        this.$refs.dialogKeyError.show()
                    }
                })
                .finally(() => {
                    this.$refs.spinning.hide()
                    this.currentKey = null
                })
        },

        openProtectionKeyDialog(ticket) {
            this.currentTicket = ticket
            this.$refs.dialogProtectionKey.show()
        },

        openProtectionEmailDialog(ticket) {
            this.currentTicket = ticket
            // this.participants[0] = Object.assign({}, this.defaultParticipant)
            for (const field of this.currentTicket.database.fields) {
                if (field.required && !this.user[field.key]) {
                    this.user[field.key] = null
                }
            }
            this.$refs['dialog-protection-email'].show()
        },

        send(evt) {
            // PREVENT DEFAULT
            evt.preventDefault()

            // VALIDATION
            for (const field of this.currentTicket.database.fields) {
                if (field.required && !this.user[field.key]) {
                    this.$refs.dialogFormError.show()
                    return
                }
            }

            setTimeout(() => {
                this.$refs['dialog-protection-email'].hide()
            }, 250)

            axios
                .post(
                    `${this.URL}/api/tickets/${this.currentTicket.id}/check-email`,
                    {
                        ...this.user,
                        return_url: window.location.href
                    }
                )
                .then(() => {
                    this.$refs.dialogEmailSent.show()
                })
        },

        getDiscountById(ticket, discountId) {
            return ticket.discounts.find(discount => {
                return discount.id === discountId
            })
        },
        closeSpecialDialogs() {
            this.refreshTickets()
        },
        prepareTicket(ticket) {
            this.otherErrors = []
            if (ticket.discount !== 0) {
                const discount = this.getDiscountById(ticket, ticket.discount)
                switch (discount.trigger) {
                    case 'quantity':
                        this.checkTicket(ticket)
                        break

                    case 'cupon':
                        this.$refs.dialogCupon.show()
                        this.currentTicket = ticket
                        this.currentTicket.trigger_value = ''
                        return

                    case 'key':
                        this.$refs.dialogKey.show()
                        this.currentTicket = ticket
                        this.currentTicket.trigger_value = ''
                        return
                }
            }
        },
        checkTicket(ticket) {
            if (ticket.discount !== 0) {
                const discount = ticket.discount
                switch (discount.trigger) {
                    case 'quantity':
                        if (ticket.amount < discount.trigger_quantity) {
                            this.missingQuantity = discount.trigger_quantity
                            this.$refs.dialogQuantity.show()
                            this.$nextTick(() => {
                                ticket.discount = 0
                            })
                        }
                        break

                    case 'cupon':
                        this.loadingCupon = true
                        axios
                            .post(
                                `${this.URL}/api/discounts/${discount.id}/check-cupon`,
                                {
                                    value: this.currentCupon
                                }
                            )
                            .then(response => {
                                if (response.data.status) {
                                    this.cuponErrors = []
                                    this.currentTicket = { discount: {} }
                                } else {
                                    switch (response.data.reason) {
                                        case 'mismatch':
                                            this.$refs.dialogMismatch.show()
                                            break

                                        case 'full':
                                            this.$refs.dialogQuota.show()
                                            break
                                    }

                                    this.currentTicket.discount = 0
                                }
                            })
                            .finally(() => {
                                this.loadingCupon = false
                                this.clearCupon()
                            })
                        break

                    case 'key':
                        this.loadingKeyValue = true
                        axios
                            .post(
                                `${this.URL}/api/discounts/${discount.id}/check-key-value`,
                                {
                                    value: discount.value
                                }
                            )
                            .then(response => {
                                if (response.data.status) {
                                    this.cuponErrors = []
                                    this.currentTicket = { discount: {} }
                                } else {
                                    this.otherErrors = ['Datos no son validos']
                                    this.currentTicket.discount = 0
                                }
                            })
                            .finally(() => {
                                this.loadingKeyValue = false
                                this.clearKeyValue()
                            })
                        break
                }
            }
            this.refreshTickets()
        },
        discountValue(ticket) {
            if (ticket.discount && ticket.discount === 0) {
                return 0
            }

            const discount = this.getDiscountById(ticket, ticket.discount)

            if (!discount) {
                return 0
            }

            switch (discount.type) {
                case 'flat':
                    return discount.type_value

                case 'percentual':
                    return (
                        (ticket.amount *
                            ticket.price *
                            discount.type_quantity) /
                        100
                    )
            }
        },
        removeFromCart(ticket) {
            this.decreaseTicketAmount(this.tickets.indexOf(ticket))
            this.checkTicket(ticket)
        },
        addToCart(ticket) {
            this.incrementTicketAmount(this.tickets.indexOf(ticket))
            this.checkTicket(ticket)
        },
        formatAmount(ticket) {
            return ticket.amount <= 9 ? 'ml-3' : ''
        },

        prevParticipant() {
            // VALIDATION CURRENT PARTICIPANT
            for (const field of this.database.fields) {
                if (
                    field.required &&
                    !this.participants[this.participantIndex][field.key]
                ) {
                    // console.log(field.name)
                    this.$refs.dialogFormError.show()
                    return
                }
            }

            this.participantIndex--
        },

        nextParticipant() {
            // VALIDATION CURRENT PARTICIPANT
            for (const field of this.database.fields) {
                if (
                    field.required &&
                    !this.participants[this.participantIndex][field.key]
                ) {
                    // console.log(this.participants[this.participantIndex])
                    this.$refs.dialogFormError.show()
                    return
                }
            }

            this.participantIndex++
        },

        submit(evt) {
            // PREVENT DEFAULT
            evt.preventDefault()

            // VALIDATION
            for (var participant of this.participants) {
                for (const field of this.database.fields) {
                    if (field.required && !participant[field.key]) {
                        this.$refs.dialogFormError.show()
                        return
                    }
                }
            }

            setTimeout(() => {
                this.$refs['dialog'].hide()
            }, 250)

            let currency = null
            this.tickets.forEach(ticket => {
                if (ticket.discount === 0) {
                    ticket.discount = null
                } else {
                    ticket.discount = this.getDiscountById(
                        ticket,
                        ticket.discount
                    )
                }

                if (ticket.amount > 0) {
                    if (!currency) {
                        currency = ticket.currency
                    } else if (currency != ticket.currency) {
                        currency = 'conflicted'
                        this.$refs.dialogOneCurrency.show()
                        return
                    }
                }
            })

            if (currency === 'conflicted') return

            switch (currency) {
                case 'CPL':
                    axios
                        .post(
                            `${this.URL}/api/events/${this.eventId}/webpay/buy`,
                            {
                                user: this.user,
                                participants: this.participants,
                                tickets: this.tickets,
                                return_url: window.location.href
                            }
                        )
                        .then(response => {
                            if (response.data.webpay.type === 'free') {
                                window.location.href = response.data.webpay.url
                                return
                            }

                            var form = document.createElement('form')
                            form.setAttribute('method', 'post')
                            form.setAttribute('id', 'popUpWebpayForm')
                            form.setAttribute(
                                'action',
                                response.data.webpay.url
                            )
                            var hiddenField = document.createElement('input')
                            hiddenField.setAttribute('name', 'TBK_TOKEN')
                            hiddenField.setAttribute(
                                'value',
                                response.data.webpay.token
                            )
                            form.appendChild(hiddenField)
                            document.body.appendChild(form)
                            form.submit()
                        })
                        .finally(() => (this.dialogOverlay = false))
                    break

                case 'USD':
                    axios
                        .post(
                            `${this.URL}/api/events/${this.eventId}/paypal/buy`,
                            {
                                user: this.user,
                                participants: this.participants,
                                tickets: this.tickets,
                                return_url: window.location.href
                            }
                        )
                        .then(response => {
                            if (response.data.paypal_url) {
                                if (response.data.paypal_url === 'free') {
                                    let url = window.location.href
                                    window.location.href =
                                        url + '?venoot_ticket_status=paid'
                                    return
                                }

                                window.location.href = response.data.paypal_url
                            }
                        })
                        .finally(() => (this.dialogOverlay = false))
                    break
            }
        },
        clear() {
            // this.$refs.form.reset()
            this.$refs.dialog.hide()
        },
        clearCupon() {
            if (this.currentTicket.discounts) {
                this.currentTicket.discount = 0
            }
            this.$refs.dialogCupon.hide()
        },
        clearKeyValue() {
            if (this.currentTicket.discounts) {
                this.currentTicket.discount = 0
            }
            this.$refs.formKeyValue.reset()
            this.$refs.dialogKey.hide()
        }
    },

    created() {
        localize('es', es)
        extend('required', required)
        extend('email', email)
        extend('min', min)

        this.setEventId(this.id)
        this.setEnv(this.env)

        this.loading = true

        // Get user if it exists
        const queryString = window.location.search
        const urlParams = new URLSearchParams(queryString)
        const access_key = urlParams.get('access_key')

        const tickets = axios
            .get(`${this.URL}/api/events/${this.eventId}/tickets-cart`)
            .then(response => {
                this.setTickets(response.data.tickets)
                this.setEvent(response.data.event)
                this.tickets.forEach(ticket => {
                    ticket.amount = 0
                    if (ticket.discounts.length > 0) {
                        this.showDiscounts = true
                        ticket.discounts.unshift({
                            name: 'Seleccione',
                            id: 0
                        })
                        ticket.discount = 0
                    }
                })
            })
            .catch(error => console.log(error))

        const database = axios
            .get(`${this.URL}/api/events/${this.eventId}/database`)
            .then(response => {
                this.setDatabase(response.data.database)
            })
            .catch(error => console.log(error))

        const checkAccess = access_key
            ? axios
                  .get(`${this.URL}/api/check-access/${access_key}`)
                  .then(response => {
                      this.emailAccepted = true
                  })
                  .catch(error => console.log(error))
            : null

        Promise.all([tickets, database, checkAccess]).finally(() => {
            // Prepare Default Participant
            for (var field of this.database.fields) {
                switch (field.type) {
                    case 'string':
                    case 'email':
                        this.defaultParticipant[field.key] = ''
                        break

                    case 'choice':
                        if (field.required) {
                            this.defaultParticipant[field.key] =
                                field.choices[0]
                        } else {
                            this.defaultParticipant[field.key] = null
                        }
                        break

                    case 'rut':
                        this.defaultParticipant[field.key] = null
                        break
                }
            }
            this.loading = false
        })
    },

    mounted() {
        const url = new URL(window.location)
        const status = url.searchParams.get('venoot_ticket_status')

        if (url.searchParams.get('names')) {
            this.names = url.searchParams.get('names').split(',')
        }

        if (url.searchParams.get('tickets')) {
            this.boughtTickets = url.searchParams.get('tickets').split(',')
        }

        if (url.searchParams.get('uuids')) {
            this.uuids = url.searchParams.get('uuids').split(',')
        }

        if (url.searchParams.get('remote')) {
            this.remote = url.searchParams.get('remote').split(',')
        }

        if (status) {
            url.searchParams.delete('venoot_ticket_status')
            url.searchParams.delete('names')
            url.searchParams.delete('tickets')
            url.searchParams.delete('uuids')
            url.searchParams.delete('remote')

            history.replaceState({ venoot: 'removed' }, '', url.href)
            switch (status) {
                case 'paid':
                    this.$refs.dialogPaid.show()
                    return

                case 'annulled':
                    this.$refs.dialogAnnulled.show()
                    return

                case 'rejected':
                    this.$refs.dialogRejected.show()
                    return
            }
        }
    }
}
</script>

<style scoped>
.venoot-pill-button {
    width: 32px;
}
</style>
